import React from 'react';
import { Form, Field } from 'react-final-form';
import { RenderInput } from '../../../components';

export const CourseForm = ({
    course = {},
    onSubmit = values => console.log(values),
    isAdd = false,
    children
}) => {

    const validate = (fields) => {
        const errors = {};
        if(!fields["phone"]) errors["phone"] = 'required';
        return errors;
    }

    return (
        <div className="course-form">
            <Form
                onSubmit={onSubmit}
                validate={validate}
                initialValues={course}
                render={({ handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                        <Field 
                            name="firstName" 
                            label="First Name"
                            component={RenderInput}
                            placeholder="First Name" 
                        />
                        <Field 
                            name="lastName" 
                            label="Last Name"
                            component={RenderInput}
                            placeholder="Last Name" 
                        />
                        <Field 
                            name="phone" 
                            label="Phone"
                            type="mobile"
                            component={RenderInput}
                            placeholder="Phone" 
                        />
                        {isAdd && <Field 
                            name="password" 
                            label="password"
                            type="password"
                            component={RenderInput}
                            placeholder="password" 
                        />}
                        <Field 
                            name="email" 
                            label="email"
                            type="email"
                            component={RenderInput}
                            placeholder="email" 
                        />
                        <Field 
                            name="latitude" 
                            label="latitude"
                            component={RenderInput}
                            placeholder="latitude" 
                        />
                        <Field 
                            name="longitude" 
                            label="longitude"
                            component={RenderInput}
                            placeholder="longitude" 
                        />
                        <Field 
                            name="country" 
                            label="country"
                            component={RenderInput}
                            placeholder="country" 
                        />
                        <Field 
                            name="city" 
                            label="city"
                            component={RenderInput}
                            placeholder="city" 
                        />
                        {children}
                    </form>
                )}
            />
        </div>
    )
}
