import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getCourses } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { formatDistanceToNow } from 'date-fns';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { CourseActions } from './components/CourseActions';
import './courses.scss';

const Courses = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getCourses((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddCourse = (newCourse) => {    
    setData(prevCourses => [newCourse, ...prevCourses]);
  };
  
  const handleEditCourse = (editedCourse) => {    
    const edit = (arr, item) => {
      return arr.map(course => {        
        if(course.id === item.id) {
          course = item;
        }
        return course;
      });
    }
    setData(prevCourses => edit(prevCourses, editedCourse))
  }
  
  const handleDeleteCourse = (courseID) => {
    setData(prevCourses => prevCourses.filter(course => {
      return course.id !== courseID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Courses <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <CourseActions 
                  showAdd
                  addCallback={handleAddCourse}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "image",
                        accessor: "image",
                        width: 75,
                        Cell: row => <Link to={`/courses/${row.original.id}`}>
                          <img 
                            src={row.value ? `data:image/png;base64,${row.value}` : PROFILE_PICTURE} 
                            width={40} 
                            height={40} 
                            style={{ borderRadius: 100 }}
                            alt="course" 
                            className="d-block mx-auto"
                          />
                        </Link>,
                        filterable: false
                      },
                      {
                        Header: "title",
                        accessor: "title",
                      },
                      {
                        Header: "category",
                        accessor: "category",
                      }
                    ]
                  },
                  {
                    Header: "Info",
                    columns: [
                      {
                        Header: "price",
                        accessor: "price"
                      },
                      {
                        Header: "city",
                        accessor: "city"
                      },
                      {
                        Header: "discountPerc",
                        accessor: "discountPerc",
                        width: 75,
                      },
                      {
                        Header: "created at",
                        accessor: "createdAt",
                        filterable: false,
                        Cell: ({ value }) => value && formatDistanceToNow(new Date(value), { addSuffix: true })
                      },
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <CourseActions 
                            showDelete 
                            showEdit 
                            course={original} 
                            deleteCallback={handleDeleteCourse}
                            editCallback={handleEditCourse}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Courses;
