import React, { useState, useEffect } from 'react';
import { ButtonGroup, Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import { CourseForm } from './CourseForm';
import { editCourse, deleteCourse, addCourse } from '../../../handlers/Api/Calls';
import { ErrorToast, FloatToast } from '../../../components';
import { LoadingButton } from '../../../components/LoadingButton';

export const CourseActions = ({
    course = {},
    showAdd = false,
    showEdit = false,
    showDelete = false,
    editCallback,
    deleteCallback,
    addCallback
}) => {
    return (
        <ButtonGroup className="course-actions">
            {showAdd && <AddModal buttonLabel="Add New Course" addCallback={addCallback} />}
            {showEdit && <EditModal buttonLabel="Edit" course={course} editCallback={editCallback} />}
            {showDelete && <DeleteModal buttonLabel="Delete" course={course} deleteCallback={deleteCallback} />}
        </ButtonGroup>
    )
}

const AddModal = ({
    buttonLabel,
    className,
    addCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleAdd = (values) => {
        addCourse(values, (apiLoading, apiData, apiError, apiSuccess) => {                        
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && data) {            
            setModal(modal => !modal);
            addCallback(data);
        }
    }, [isSuccess, data]);

    return (
        <>
            <Button color="primary" onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data added successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Add New Course</ModalHeader>
                <ModalBody>
                    <CourseForm isAdd onSubmit={handleAdd}>
                        {error && <ErrorToast error={error} />}
                        <ModalFooter>
                            <LoadingButton loading={isLoading} color="primary" type="submit">Add</LoadingButton>{' '}
                            <Button onClick={toggle}>Cancel</Button>
                        </ModalFooter>
                    </CourseForm>
                </ModalBody>
            </Modal>
        </>
    );
}

const EditModal = ({
    course,
    buttonLabel,
    className,
    editCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleEdit = (values) => {
        editCourse(values, (apiLoading, apiData, apiError, apiSuccess) => {
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && data) {
            setModal(modal => !modal);
            editCallback(data);
        }
    }, [isSuccess, data]);

    return (
        <>
            <Button onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data edited successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Edit {course['email']}</ModalHeader>
                <ModalBody>
                    <CourseForm
                        onSubmit={handleEdit}
                        course={course}
                    >
                        {error && <ErrorToast error={error} />}
                        <ModalFooter>
                            <LoadingButton loading={isLoading} color="primary" type="submit">Edit</LoadingButton>{' '}
                            <Button onClick={toggle}>Cancel</Button>
                        </ModalFooter>
                    </CourseForm>
                </ModalBody>
            </Modal>
        </>
    );
}

const DeleteModal = ({
    course,
    buttonLabel,
    className,
    deleteCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    // const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleDelete = () => {
        deleteCourse(course.id, (apiLoading, apiData, apiError, apiSuccess) => {
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            // setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && course.id) {
            setModal(modal => !modal);            
            deleteCallback(course.id);
        }
    }, [isSuccess]);

    return (
        <>
            <Button color="danger" onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data deleted successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Delete {course['email']}</ModalHeader>
                <ModalBody>
                    {error && <ErrorToast error={error} />}
                    Are you sure you want to delete this course!
                </ModalBody>
                <ModalFooter>
                    <LoadingButton loading={isLoading} color="danger" onClick={handleDelete}>Delete</LoadingButton>{' '}
                    <Button onClick={toggle}>Cancel</Button>
                </ModalFooter>
            </Modal>
        </>
    );
}